import tabControl from '@/components/tabControl-tag/tabControl-tag.vue';
export default {
	components: { tabControl },
	data() {
		return {
			items: ['发现', '社区'],
			current: 0,
			// 社区用户信息
			user: [0, 1, 2, 2]
		};
	},
	methods: {
		onClickItem(val) {
			this.current = val.currentIndex;
		},
		scollSwiper(e) {
			this.current = e.target.current;
		}
	}
};
